import React, { useEffect, useState } from "react";
import { ThemeProvider } from "styled-components";
import "./App.css";
import { useDarkMode } from "./DarkMode";
import Main from "./containers/Main";
import { GlobalStyles } from "./global";
import Splash from "./pages/splash/Splash";
import { settings } from "./portfolio";
import { themes } from "./theme";
function App() {
const [loading, setLoading] = useState(true);
const duration = 6500;
useEffect(()=>{
  setTimeout(() => {
    setLoading(false);
  }, duration);
},[]);
const [themeMode, toggleTheme] = useDarkMode();
const theme = themeMode === 'light'?themes.materialLightTheme:themes.materialDarkTheme;
  return (
    <ThemeProvider theme={theme}>
      <>
        <GlobalStyles />
        <div>
          {(loading===false || settings.isSplash===false)?
          <Main theme={theme} themeMode={themeMode} toggleTheme={toggleTheme} duration={duration}/>:
          <Splash theme={theme} close={()=>{}} duration={duration} />
          }
        </div>
      </>
    </ThemeProvider>
  );
// return (<LoaderLogo id="logo" theme={theme}/>);
}

export default App;
